body {
  background-color: #7f7f7f;
  justify-content: center;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.people {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
  gap: 20px;
  display: grid;
}

@media (width <= 1000px) {
  .people {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }
}

.person {
  text-align: center;
  border: 1px solid #ccc;
  justify-content: center;
  width: 200px;
  height: 231px;
}

.photo img {
  width: 200px;
  height: 200px;
}

.name {
  text-align: center;
  font-size: 20px;
  line-height: 23px;
}

.extra {
  text-align: center;
  font-size: 22px;
  line-height: 25px;
}
